// Variable
$control-color: #204264;

html {
  background: #204264;
}

.controls {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  width: 100vw;
  height: 100vh;
}

.control {
  width: 80%;
  text-align: center;
  vertical-align: center;
  line-height: 100px;
  margin: 10px;
  background: $control-color;
  color: white;
  border: solid white 3px;

  cursor: pointer;

  &:hover {
    background: white;
    color: $control-color;
    border-color: $control-color;
  }
}
